@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* リキッドレイアウト対応 */
html {
  font-size: 4.26667vw;
}

@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}

@media screen and (max-width: 1023px) {
  html {
    font-size: 1.45455vw;
  }
}

@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Noto Serif JP", serif;
  font-size: 16px;
  font-weight: 500;
  color: #222;
  line-height: 2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #F3EFEB;
  overflow-wrap: break-word;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  position: relative;
  z-index: -9999;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  border: none;
  vertical-align: top;
}

video,
iframe,
object {
  max-width: 100%;
  height: auto;
  border: none;
}

ul,
ol {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  table-layout: fixed;
  border: 1px solid;
}

th,
td {
  padding: 4px 6px;
  text-align: left;
  vertical-align: top;
  border: 1px solid;
}

blockquote,
q {
  quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
  content: "";
  content: none;
}

address {
  font-style: normal;
}

b,
strong {
  font-weight: 700;
}

form {
  margin: 0;
  padding: 0;
}

label {
  display: inline-block;
}

a,
area,
button,
input,
label,
select,
summary,
textarea,
[tabindex] {
  touch-action: manipulation;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
select {
  text-transform: none;
}

select {
  cursor: pointer;
}

textarea {
  overflow: auto;
  -ms-overflow-style: scrollbar;
  resize: vertical;
}

dialog {
  background-color: inherit;
  border: solid;
  color: inherit;
  display: block;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: fit-content;
}

dialog:not([is-open]) {
  display: none;
}

summary {
  display: list-item;
  cursor: pointer;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

.l-inner {
  margin: 0 auto;
  max-width: 1150px;
  width: 100%;
  height: inherit;
  padding-right: 25px;
  padding-left: 25px;
}

@media screen and (max-width: 1023px) {
  .l-inner {
    max-width: 33.75rem;
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
  }
}

.l-inner--wide {
  max-width: 1230px;
}

.l-inner--slim {
  max-width: 738px;
}

.c-btn {
  display: inline-block;
  background: #222;
  color: #fff;
  font-weight: 700;
  line-height: 1.4375;
  padding: 1.5625rem;
  width: 100%;
  max-width: 17.5rem;
  text-align: center;
  position: relative;
  transform: translate(-0.375rem, -0.375rem);
  transition: 0.3s transform;
}

.c-btn::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  border-bottom: 1px #222 solid;
  border-right: 1px #222 solid;
  top: 0.375rem;
  left: 0.375rem;
}

.c-btn:hover {
  transform: initial;
}

.c-btn:hover::before {
  display: none;
}

.c-tab-btn {
  display: inline-block;
  background: #382620;
  color: #fff;
  font-weight: 700;
  line-height: 1.25;
  padding: 1.4375rem;
  width: 100%;
  max-width: 12.5rem;
  text-align: center;
  position: relative;
}

.c-tab-btn::before {
  position: absolute;
  content: "";
  width: calc(100% - 0.25rem * 2);
  height: calc(100% - 0.25rem * 2);
  border: 1px #fff solid;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.c-tab-btn--disable {
  background: #888;
}

.u-mobile {
  display: none;
}

@media screen and (max-width: 1023px) {
  .u-mobile {
    display: block;
  }
}

@media screen and (max-width: 1023px) {
  .u-desktop {
    display: none;
  }
}

body {
  font-size: 1rem;
}
